class DrawImg{
	constructor(ctx, imgInfo, { left, top, alpha, cavWidth, cavHeight, effect, isToAlapha, diffWidth }){
		Object.assign(this, {
			ctx,
			originWidth: imgInfo.width,
			img: imgInfo.el,
			imgWidth: imgInfo.width - diffWidth, //
			imgHeight: imgInfo.height - diffWidth,
			x: left, //x 
			y: top, //y 
			alpha,
			effect,
			isToAlapha,
			cavWidth,
			oneUnit: cavHeight/4, //canvan32/4,1/4
			toRight: (Math.random() > 0.5 ? false : true), //
			ranX: (Math.random()*2.5 - 1.25)/2, //
		});
		this.updateCtx = this.updateCtx.bind(this);
		this.effectCommon = this.effectCommon.bind(this);
	}
	addCtx(){
		const p = this, 
				{ ctx } = p;
		ctx.save();
		ctx.globalAlpha = p.alpha;
		ctx.drawImage(p.img, p.x, p.y, p.imgWidth, p.imgHeight);
		ctx.restore();
	}
	setImgWidth(){
		const { originWidth, imgWidth } = this;
		if(imgWidth < originWidth){//diffWidth
		  this.imgWidth += 1;
		  this.imgHeight += 1;
		}
	}
	setAlapa(){
		const p = this,
					{ y, isToAlapha, oneUnit } = this;
		if(!isToAlapha)return false; //
		if(y < oneUnit){ //
		  if(p.alpha <= 0.02){
		    p.alpha = 0;
		  }else{
		    p.alpha -= 0.02;
		  }
		}else if(y > oneUnit && y < oneUnit*2){ //
		  p.alpha -= 0.01;
		}
	}
	setYpx(){
		const p = this,
					{ y, effect, countY, oneUnit } = p,
					yPxs = countY(effect);
		// 
		switch(true){ 
			case y < oneUnit: //
				p.y -= yPxs[0];
				break;
			case y > oneUnit && y < oneUnit*2: //
				p.y -= yPxs[1];
				break;
			default:
				p.y -= yPxs[2];
		}
	}
	setDivergeCommonX(){//pullbackx
		const p = this,
					{ y, ranX, oneUnit } = p;
		// 
		if(y < oneUnit){ //
		  if(Math.random() > 0.5){ //x
		    p.x += ranX/2;
		  }
		}else if(y > oneUnit && y < oneUnit*2){ //
		  p.x += ranX/2;
		}else{//
		  p.x += ranX;
		}
	}
	easeEffect(){//y
		this.setDivergeCommonX();
		this.effectCommon();
	}
	linearEffect(){//y
		this.setDivergeCommonX();
		this.effectCommon();
	}
	pullbackEffect(){//x
		const p = this,
					{ y, cavWidth, ranX, originWidth } = p,
					_ranX = Math.abs(ranX)*2;
		// 
		switch(true){
			case (p.x + originWidth >= cavWidth): //
				p.toRight = false;
				p.x -= _ranX;
				break;
			case p.x <= 2: //
				p.toRight = true;
				p.x += _ranX;
				break;
			case p.toRight:
				p.x += _ranX;
				break;
			default:
				p.x -= _ranX;
		}
		this.effectCommon();
	}
	effectCommon(){
		this.setYpx();
		this.setAlapa();
		this.setImgWidth();
	}
	countY(effect){//effecty3y3
		switch(effect){
			case 'ease':
				return [2, 3.5, 3];
			case 'linear':
				return [3, 3, 3];
			case 'pullback':
				return [1.5, 2.5, 2];
			default:
				return [2, 3.5, 3];
		}
	}
	updateCtx(){
		this[`${this.effect}Effect`]();
	}
}

export default class JumpBubble{
	/*
	** @param canvasNode   [] <DOM> canvan
	** @param config       [] <Obj> 
	**    - effect      <Str> ,lineareasepullback'ease'
	**    - isToAlapha  <Boo>  true
	**    - alpha       <Num>  0.9
	**    - diffWidth   <Num> width15
	**    - left        <Num> 15
	**    - top         <Num> 30
	**    - width       <Num>  30
	** @param  callback    [] <Func> 
	*/
	constructor(canvasNode, config = {}, callback ){
		if(!canvasNode || !canvasNode.getContext){
		  console.warn("jumpBufflecanvas  canvas");
		  this.error = true;
		  return false;
		}
		const t = this,
		    { width, height } = canvasNode;
		const _config = { //
		  width: 30, //
		  left : width/2 - 15,  //
		  top : height - 30, //
		  alpha : 0.9,  // 
		  effect: 'ease',
		  isToAlapha: true,
		  diffWidth: 15, // 15
		  cavHeight: height,  // [] canvan
		  cavWidth: width,  	// [] canvan
		};
		Object.assign(t, {
			canvasInfo: {
				canvas : canvasNode,
				width,
				height
			},
			config: Object.assign(_config, config),
			ctx: canvasNode.getContext("2d"),
			bubbleArr: [],//
			allImg: { //img
				lists: [], //src
				doms: [], //listdom
				loadState: [], //imgimgdomimg.loadimgdom
			}
		});
		callback && callback(t);
	}
	/*
	** 
	** before: 
	** after: 
	*/
	create(imgsrc, before, after){
		const t = this,
					{ctx, error} = t;
		if(!ctx || error){
		  console.warn("jumpBufflecreatectx");
		  return false;
		}
		const { bubbleArr, canvasInfo } = t,
		    	{ width: imgwidth } = t.config;
		t.createImg(imgsrc).then(imgNode => {
			const imgInfo = {
			  el : imgNode,
			  width : imgwidth || imgNode.width,
			  height : imgwidth && imgNode.height*(imgwidth/imgNode.width) || imgNode.height
			};
			if(bubbleArr.length>30){
			  return;
			}
			bubbleArr.push(new DrawImg(ctx, imgInfo, t.config));
			//, 
			before && before(canvasInfo.canvas, imgNode, bubbleArr);  
			if(!t.setInter){
			  t.setInterFn(after);
			}
		});
		return this;
	}
	createImg(imgsrc){
		return new Promise(res => {
			const { lists, doms, loadState } = this.allImg;
			const i = lists.indexOf(imgsrc);
			if(i > -1 && loadState[i]){
				res(doms[i]);
				return ;
			}
			const img = document.createElement('img');
			img.src = imgsrc;
			img.setAttribute('style', 'display:none;');
			document.body.appendChild(img);
			lists.push(imgsrc);
			doms.push(img);
			img.onload = () => {
				loadState.push(true);
				res(img);
			}
		})
	}
	setInterFn(after){
		const t = this, 
					{ ctx, canvasInfo } = t,
		    	{ width, height } = canvasInfo;
		t.setInter = setInterval(function(){
		  try{
		  	ctx.clearRect(0, 0, width, height);
		  	t.bubbleArr = t.bubbleArr.filter(function(val){
		  	  val.addCtx();
		  	  val.updateCtx();
		  	  if(val.y < 10){
		  	  	after && after();
		  	    return false;
		  	  }else{
		  	    return true;
		  	  }
		  	});
		  	if(t.bubbleArr.length === 0){
		  	  clearInterval(t.setInter);
		  	  t.setInter = null;
		  	  ctx.clearRect(0, 0, width, height);
		  	}
		  }catch(e){
		  	console.warn('',e);
		  	clearInterval(t.setInter);
		  }
		},25);
	}
}





